<template>
  <div id="page-user-view">
    <div id="user-data">
      <vx-card class="mb-4 vs-con-loading__container" id="div-with-loading">
        <div class="mb-8">
          <a href="javascript:void()" @click="$router.go(-1)">← Back</a>
        </div>
        <!-- Avatar -->
        <div class="vx-row">
          <!-- Avatar Col -->
          <div class="vx-col" id="avatar-col">
            <div class="img-container mb-4">
              <img
                v-if="churchInfo.img"
                :src="'https://d1fp61h5e226jt.cloudfront.net/'+churchInfo.img"
                class="logowidth"
              />
            </div>
          </div>

          <!-- Information - Col 1 -->
          <div class="vx-col flex-1" id="account-info-col-1">
            <table>
              <tr>
                <td class="font-semibold">Church Name</td>
                <td>{{churchInfo.name}}</td>
              </tr>
              <tr>
                <td class="font-semibold">Location</td>
                <td>{{churchInfo.region}} {{churchInfo.state}},{{churchInfo.country}}</td>
              </tr>
              <tr>
                <td class="font-semibold">Address</td>
                <td>{{churchInfo.address}}</td>
              </tr>
              <tr>
                <td class="font-semibold">Email</td>
                <td>
                  <a :href="'mailto:'+churchInfo.email" target="_blank">{{churchInfo.email}}</a>
                </td>
              </tr>
            </table>
            <div class="vx-col w-full flex mt-5" id="account-manage-buttons">
              <vs-button
                color="dark"
                class="mr-4"
                @click="$router.push({
                  path: '/church-branch',
                  query: { churchId: $route.params.id, churchName: churchInfo.name , publicToken: churchInfo.userId.publicToken },
                })"
              >Branch</vs-button>
              <vs-button
                icon-pack="feather"
                icon="icon-edit"
                class="mr-4"
                @click="$router.push({
                  path: '/edit-church/'+churchInfo.userId,
                  query: {  _id: churchInfo._id },
                })"
              >Edit</vs-button>
              <vs-button
                type="border"
                color="danger"
                icon-pack="feather"
                icon="icon-trash"
                @click="confirmDeleteRecord"
              >Delete</vs-button>
            </div>
          </div>
          <!-- /Information - Col 1 -->

          <!-- Information - Col 2 -->
          <div class="vx-col flex-1" id="account-info-col-2">
            <table>
              <tr>
                <td class="font-semibold">Pastor</td>
                <td>{{churchInfo.pastor}}</td>
              </tr>
              <tr>
                <td class="font-semibold">Mobile Number</td>
                <td>{{churchInfo.countryCode}}{{churchInfo.phoneNumber}}</td>
              </tr>
              <tr>
                <td class="font-semibold">Zone</td>
                <td>{{churchInfo.zone}}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 2 -->
        </div>
      </vx-card>

      <div class="vx-row">
        <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
          <vx-card
            class="mb-base vs-con-loading__container"
            title="Church Wallet"
            id="div-with-wallet"
          >
            <div class="vx-row">
              <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-3">
                <div class="walletinfocard">
                  <h3 class="font-bold">{{totalAmt | currency('₦', 0)}}</h3>
                  <p class="text-danger">Total</p>
                </div>
              </div>
              <div
                v-for="(wallet,index) in walletInfo"
                :key="index"
                class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 mb-3"
              >
                <div class="walletinfocard">
                  <h3 class="font-bold">{{wallet.amount | currency('₦', 0)}}</h3>
                  <p class="text-danger">{{wallet.name}}</p>
                </div>
              </div>
            </div>
          </vx-card>
        </div>
        <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
          <vx-card
            :title="'Wallet History'"
            class="mb-base vs-con-loading__container"
            id="div-with-wallet"
          >
            <div class style="min-height: 240px;">
              <e-charts
                style="min-height: 240px"
                class="piechartheight"
                autoresize
                :options="walletChart"
                ref="pie"
                auto-resize
              />
            </div>
          </vx-card>
        </div>
      </div>
      <vx-card :title="'Wallet Transactions'" class="mb-base vs-con-loading__container">
        <vs-table
          id="div-with-transactions"
          search
          max-items="5"
          pagination
          :data="transactionInfo"
        >
          <template slot="thead">
            <!-- <vs-th sort-key="email">Id</vs-th> -->
            <!-- <vs-th>Id</vs-th> -->
            <vs-th>Date created</vs-th>
            <vs-th>Member</vs-th>
            <vs-th>Description</vs-th>
            <vs-th>Amount</vs-th>
            <vs-th>Status</vs-th>
            <vs-th>Type</vs-th>
            <vs-th>Action</vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data">
              <!-- <vs-td :data="tr.id">{{ index + 1 }}</vs-td> -->
              <vs-td :data="tr.createdAt">{{ tr.createdAt | formatDate }}</vs-td>
              <vs-td :data="tr.memberId.fullName">{{ tr.memberId.fullName }}</vs-td>
              <vs-td :data="tr.description">{{tr.description}}</vs-td>
              <vs-td :data="tr.amount">{{tr.amount | currency('₦', 0)}}</vs-td>
              <vs-td :data="tr.status">
                <vs-chip>
                  <span class="uppercase">{{tr.status}}</span>
                </vs-chip>
              </vs-td>
              <vs-td :data="tr.type">
                <span class="capitalize">{{tr.type}}</span>
              </vs-td>
              <vs-td>
                <vs-button
                  @click="viewTransaction(tr._id)"
                  color="primary"
                  type="filled"
                  size="small"
                  class="mr-2 mb-2"
                >View</vs-button>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </vx-card>

      <!-- Transaction Pop-up -->
      <vs-popup class="holamundo" title="Transaction Details" :active.sync="transactionPopUp">
        <p class="mb-4 pl-2">
          <span class="font-bold">Date -</span>
          {{transactionSingle.date | formatDate}}
        </p>
        <div class="vx-row px-2">
          <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
            <div class="walletinfocard">
              <h3 class="font-bold">{{transactionSingle.totalamt | currency('₦', 0)}}</h3>
              <p class="text-danger">Total</p>
            </div>
          </div>
          <div class="vx-col w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
            <div class="walletinfocard">
              <vs-list-item
                :title="transactionSingle.membername"
                :subtitle="transactionSingle.memberemail"
              >
                <template slot="avatar">
                  <vs-avatar
                    :src="'https://d1fp61h5e226jt.cloudfront.net/'+transactionSingle.avatar"
                  />
                </template>
              </vs-list-item>
            </div>
          </div>
        </div>

        <vs-list>
          <vs-list-item title="Description" :subtitle="transactionSingle.description"></vs-list-item>
          <vs-list-item title="Mode of Payment" :subtitle="transactionSingle.mode"></vs-list-item>
          <vs-list-item title="Payment GateWay" :subtitle="transactionSingle.gateway"></vs-list-item>
          <vs-list-item title="Recursive" :subtitle="transactionSingle.recursive"></vs-list-item>
          <vs-list-item title="Transaction Reference" :subtitle="transactionSingle.transref"></vs-list-item>
          <vs-list-item title="Type" :subtitle="transactionSingle.type"></vs-list-item>

          <div class="mb-5 mt-3">
            <vs-list-header title="Wallet Information"></vs-list-header>
            <vs-list-item
              title="Amount"
              :subtitle="transactionSingle.wallet.amount | currency('₦', 0)"
            ></vs-list-item>
            <vs-list-item title="Name" :subtitle="transactionSingle.wallet.name"></vs-list-item>
            <vs-list-item title="Verified" :subtitle="transactionSingle.wallet.verified"></vs-list-item>
          </div>
        </vs-list>
      </vs-popup>
    </div>
  </div>
</template>

<script>
import ECharts from "vue-echarts/components/ECharts";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/chart/bar";
import "echarts/lib/chart/pie";
export default {
  data() {
    return {
      user_data: null,
      totalAmt: 0,
      user_not_found: false,
      transactionPopUp: false,
      churchInfo: {},
      walletInfo: {},
      transactionInfo: [],
      transactionSingle: {
        date: "",
        totalamt: "",
        membername: "",
        avatar: "",
        memberemail: "",
        description: "",
        mode: "",
        gateway: "",
        recursive: "",
        transref: "",
        type: "",
        wallet: {
          amount: "",
          name: "",
          verified: ""
        }
      },
      walletChart: {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: []
        },
        series: [
          {
            name: "Customers",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            color: [
              "#0f163a",
              "#768cff",
              "#473DC1",
              "#0DAC99",
              "#27CB98",
              "#D5C723",
              "#DA52E5",
              "#F59781"
            ],
            data: [],
            itemStyle: {
              emphasis: {
                shadowBlur: 7,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.2)"
              }
            }
          }
        ]
      }
    };
  },
  components: {
    ECharts
  },
  computed: {
    // userAddress() {
    //   let str = "";
    //   for (const field in this.user_data.location) {
    //     str += `${field} `;
    //   }
    //   return str;
    // }
  },
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    openLoadingInWAllet() {
      this.$vs.loading({
        container: "#div-with-wallet",
        scale: 0.6
      });
    },
    openLoadingInTransactions() {
      this.$vs.loading({
        container: "#div-with-transactions",
        scale: 0.6
      });
    },
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: "confirm",
        color: "danger",
        title: "Confirm Delete",
        text: `You are about to delete "${this.churchInfo.name}"`,
        accept: this.deleteAccount,
        acceptText: "Delete"
      });
    },
    viewTransaction(transactionid) {
      this.transactionPopUp = true;
      let singleTrans = this.transactionInfo.find(
        transaction => transaction._id == transactionid
      );
      // console.log(singleTrans);
      this.transactionSingle = {
        date: singleTrans.createdAt,
        totalamt: singleTrans.amount.toString(),
        status: singleTrans.status,
        membername: singleTrans.memberId.fullName,
        memberemail: singleTrans.memberId.email,
        description: singleTrans.description,
        mode: singleTrans.modeOfPayment,
        gateway: singleTrans.paymentGateWay,
        recursive: singleTrans.recursive.toString(),
        transref: singleTrans.txRef,
        type: singleTrans.type,
        avatar: singleTrans.memberId.imgx250,
        wallet: {
          amount: singleTrans.walletId.amount.toString(),
          name: singleTrans.walletId.name,
          verified: singleTrans.walletId.verified.toString()
        }
      };
    },
    getChurchInfo() {
      const churchId = this.$route.params.id;
      this.$store
        .dispatch("getChurchDetail", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.churchInfo = resp.data;
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          console.log(err.response);

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Account",
                text: "Unable to load church details, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to load church details, try again!",
              color: "warning"
            });
          }
        });
    },
    getChurchWallet() {
      const churchId = this.$route.params.id;
      this.$store
        .dispatch("getChurchWallet", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-wallet > .con-vs-loading");
          this.walletInfo = resp.data;
          // console.log(this.walletInfo);
          for (let i = 0; i < this.walletInfo.length; i++) {
            this.walletChart.legend.data.push(this.walletInfo[i].name);
            let chartData = {
              value: this.walletInfo[i].amount,
              name: this.walletInfo[i].name
            };
            this.totalAmt += this.walletInfo[i].amount;
            this.walletChart.series[0].data.push(chartData);
          }
          // console.log(this.walletChart.legend.data);
          // console.log(this.walletChart.series.data);

          // this.walletInfo = resp.data;
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-wallet > .con-vs-loading");
          console.log(err.response);

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Wallet",
                text: "Unable to load church wallet, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to load church wallet, try again!",
              color: "warning"
            });
          }
        });
    },

    getChurchTransaction() {
      const churchId = this.$route.params.id;
      this.$store
        .dispatch("getChurchTransaction", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-transactions > .con-vs-loading");
          this.transactionInfo = resp.data.transaction;
          // console.log(resp.data.transaction);
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-transactions > .con-vs-loading");
          console.log(err.response);

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Wallet",
                text: "Unable to load church transactions, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to load church transactions, try again!",
              color: "warning"
            });
          }
        });
    },
    deleteAccount() {
      this.openLoadingInDiv();
      let churchId = this.churchInfo._id;
      this.$store
        .dispatch("deleteChurch", churchId)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          // console.log(resp);
          this.showDeleteSuccess();
          this.$router.push("/account-church");
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          console.log(err.response);
          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Church Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Church Account",
                text: "Unable to delete church account, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Church Account",
              text: "Unable to delete church account, try again!",
              color: "warning"
            });
          }
        });
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: "success",
        title: "Account Deleted",
        text: "The selected church account was successfully deleted"
      });
    }
  },
  mounted() {
    this.openLoadingInDiv();
    this.openLoadingInWAllet();
    this.openLoadingInTransactions();
    this.getChurchInfo();
    this.getChurchWallet();
    this.getChurchTransaction();
  }
};
</script>

